.nav {
  max-width: 12em;
  background: linear-gradient(0.25turn, var(--BackgroundColor),var(--BackgroundColor),var(--BackgroundColor), transparent);
}
.title {
  text-align: center;
  /* background-color: var(--BackgroundColor60); */
  font-size: 1.2em;
  font-weight: bold;
}
.controls {
  display: flex;
  input {
    width: 100%;
    background-color: var(--BackgroundColor);
    border: 0.25em solid var(--DarkRed);
    margin: 0.25em;
    font-size: 0.8em;
    &:focus{
      outline: var(--Focus);
    }
  }
}
.button {
  width: 100%;
  background-color: var(--DarkRed);
  border: 0.25em solid black;
  margin: 0.25em;
  color: var(--BackgroundColor);
  font-size: 0.8em;
  &:focus{
    outline: var(--Focus);
  }
}
.expanded {
  width: calc(100% - 0.5em);
}
.collapsed {
  height: 7em;
  width: 2em;
}
