.wrapper {
  position: relative;
  padding: 0.3em 0;
}

.line {
  position: absolute;
  height: 0.125em;
}

.marker_wrapper {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  .year {
    position: absolute;
    font-size: 1.5em;
    /* background-color: var(--BackgroundColor40);
    border-radius: .25em;
    box-shadow: 0 0 .5em var(--BackgroundColor80); */
    margin: 0;
  }
}
.marker {
  height: 0.25em;
  width: 0.25em;
  background-color: var(--DarkRed);
  transform: rotate(45deg) translateY(-50%);
  border: 0.2em solid black;
}

.arrowhead {
  position: fixed;
  right: 0;
  transform: translatey(-0.45em);
  border-left: 1em solid black;
  border-top: 0.5em solid transparent;
  border-bottom: 0.5em solid transparent;
}