.label{
  display: inline;
  color: var(--BackgroundColor);
  font-size: 2em;
  font-weight: bold;
  display: inline-block;
}
.time_period {
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    height: 100%;
    position: absolute;
    text-align: center;
    border: none;
    &:focus{
      outline: var(--Focus);
    }
  }