.main {
  flex: 1;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  background-color: var(--BackgroundColor);
}
.nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh; /* 100% of the viewport height */
  position: fixed; /* Fix the nav panel */
  top: 0;
  left: 0;
  background: linear-gradient(0.25turn, var(--BackgroundColor60), transparent);
}
.timelines_wrapper {
  flex: 1; /* Takes remaining space */
  background-color: var(--BackgroundColor);
  overflow-y: auto;
  display: flex;
  align-items: center;
}
.timelines {
  display: block;
}